        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                按钮用于触发一个操作，如提交表单。
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">
                <div class="hb-a-lis">
                    <Btn type="default">默认按钮</Btn>
                    <Btn type="primary" >主要按钮</Btn>
                    <Btn type="warning" >警告按钮</Btn>
                    <Btn type="danger" >危险按钮</Btn>
                </div>
                <div class="hb-a-lis">
                    <Btn type="default" plain >朴素按钮</Btn>
                    <Btn type="primary" plain >主要按钮</Btn>
                    <Btn type="warning" plain >警告按钮</Btn>
                    <Btn type="danger" plain >危险按钮</Btn>
                </div>
                <div class="hb-a-lis">
                    <Btn type="default" round >圆角按钮</Btn>
                    <Btn type="primary" round >主要按钮</Btn>
                    <Btn type="warning" round >警告按钮</Btn>
                    <Btn type="danger" round >危险按钮</Btn>
                </div>
            </div>

