                <div class="hb-cmain">
                    <div class="hb-code-desc">使用type、plain、round属性来定义 Button 的样式。</div>
                    <div v-highlight>
                        <pre>
                            <code >
                &lt;wl-button type="default" &gt;默认按钮&lt;/wl-button&gt;
                &lt;wl-button type="primary" &gt;主要按钮&lt;/wl-button&gt;
                &lt;wl-button type="warning" &gt;警告按钮&lt;/wl-button&gt;
                &lt;wl-button type="danger" &gt;危险按钮&lt;/wl-button&gt;
                ---------------------------------------------------------
                &lt;wl-button type="default" plain &gt;默认按钮&lt;/wl-button&gt;
                &lt;wl-button type="primary" plain &gt;主要按钮&lt;/wl-button&gt;
                &lt;wl-button type="warning" plain &gt;警告按钮&lt;/wl-button&gt;
                &lt;wl-button type="danger" plain &gt;危险按钮&lt;/wl-button&gt;
                ---------------------------------------------------------
                &lt;wl-button type="default" round &gt;圆角按钮&lt;/wl-button&gt;
                &lt;wl-button type="primary" round &gt;主要按钮&lt;/wl-button&gt;
                &lt;wl-button type="warning" round &gt;警告按钮&lt;/wl-button&gt;
                &lt;wl-button type="danger" round &gt;危险按钮&lt;/wl-button&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">禁用状态</div>
            <div class="hb-a">
                <div class="hb-a-lis">
                    <Btn type="default" disabled>默认按钮</Btn>
                    <Btn type="primary" disabled>主要按钮</Btn>
                    <Btn type="warning" disabled>警告按钮</Btn>
                    <Btn type="danger" disabled>危险按钮</Btn>
                </div>
                <div class="hb-a-lis">
                    <Btn type="default" plain disabled>朴素按钮</Btn>
                    <Btn type="primary" plain disabled>主要按钮</Btn>
                    <Btn type="warning" plain disabled>警告按钮</Btn>
                    <Btn type="danger" plain disabled>危险按钮</Btn>
                </div>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                &lt;wl-button type="default" disabled &gt;默认按钮&lt;/wl-button&gt;
                &lt;wl-button type="primary" disabled &gt;主要按钮&lt;/wl-button&gt;
                &lt;wl-button type="warning" disabled &gt;警告按钮&lt;/wl-button&gt;
                &lt;wl-button type="danger" disabled &gt;危险按钮&lt;/wl-button&gt;
                 -----------------------------------------------------------------
                &lt;wl-button type="default" plain disabled &gt;默认按钮&lt;/wl-button&gt;
                &lt;wl-button type="primary" plain disabled &gt;主要按钮&lt;/wl-button&gt;
                &lt;wl-button type="warning" plain disabled &gt;警告按钮&lt;/wl-button&gt;
                &lt;wl-button type="danger" plain disabled &gt;危险按钮&lt;/wl-button&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">文字按钮</div>
            <div class="hb-a">
                <Btn type="text" >文字按钮</Btn>
                <Btn type="text" disabled >文字按钮</Btn>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div class="hb-code-desc">没有边框和背景色的按钮。</div>
                    <div v-highlight>
                        <pre>
                            <code >
                &lt;wl-button type="text" &gt;文字按钮&lt;/wl-button&gt;
                &lt;wl-button type="text" disabled &gt;文字按钮&lt;/wl-button&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">图标按钮</div>
            <div class="hb-a">
                <div class="hb-a-lis">
                    <Btn type="default" icon="iconicon-xitongshouye" >默认按钮</Btn>
                    <Btn type="primary" icon="iconfuxuan-xuanzhong" >主要按钮</Btn>
