                    <Btn type="default" plain icon="iconicon-xitongshouye">朴素按钮</Btn>
                    <Btn type="primary" plain icon="iconfuxuan-xuanzhong">主要按钮</Btn>
                    <Btn type="warning" plain icon="iconicon-denglu-mima">警告按钮</Btn>
                    <Btn type="danger" plain icon="iconicon-denglu-mima">危险按钮</Btn>
                </div>
                <div class="hb-a-lis">
                    <Btn type="default" icon="iconicon-xitongshezhi"></Btn>
                    <Btn type="primary" icon="iconicon-yunyingzhongxin"></Btn>
                    <Btn type="warning" icon="iconfuwu"></Btn>
                    <Btn type="danger" icon="iconicon-caidanlan-shangpin"></Btn>
                </div>
                <div class="hb-a-lis">
                    <Btn type="default" icon="iconicon-xitongshezhi" circle></Btn>
                    <Btn type="primary" icon="iconicon-yunyingzhongxin" circle></Btn>
                    <Btn type="warning" icon="iconfuwu" circle></Btn>
                    <Btn type="danger" icon="iconicon-caidanlan-shangpin" circle></Btn>
                </div>
            </div>

            <div class="hb-code">
                <div class="hb-cmain">
                    <div class="hb-code-desc"> 使用icon属性，为按钮添加图标</div>
                    <div v-highlight>
                        <pre>
                            <code >
                &lt;wl-button type="primary" icon="iconicon-xitongshouye" &gt;主要按钮&lt;/wl-button&gt;
                &lt;wl-button type="default" icon="iconfuxuan-xuanzhong" &gt;默认按钮&lt;/wl-button&gt;
                &lt;wl-button type="warning" icon="iconicon-denglu-mima" &gt;警告按钮&lt;/wl-button&gt;
                &lt;wl-button type="danger" icon="iconicon-denglu-mima" &gt;危险按钮&lt;/wl-button&gt;
                --------------------------------------------------------------------------------------
                &lt;wl-button type="primary" icon="iconicon-xitongshouye" plain &gt;主要按钮&lt;/wl-button&gt;
                &lt;wl-button type="default" icon="iconfuxuan-xuanzhong" plain &gt;默认按钮&lt;/wl-button&gt;
                &lt;wl-button type="warning" icon="iconicon-denglu-mima" plain &gt;警告按钮&lt;/wl-button&gt;
                &lt;wl-button type="danger" icon="iconicon-denglu-mima" plain &gt;危险按钮&lt;/wl-button&gt;
                --------------------------------------------------------------------------------------
                &lt;wl-button type="primary" icon="iconicon-xitongshezhi" &gt;&lt;/wl-button&gt;
                &lt;wl-button type="default" icon="iconicon-yunyingzhongxin" &gt;&lt;/wl-button&gt;
                &lt;wl-button type="warning" icon="iconfuwu" &gt;&lt;/wl-button&gt;
                &lt;wl-button type="danger" icon="iconicon-caidanlan-shangpin" &gt;&lt;/wl-button&gt;
                --------------------------------------------------------------------------------------
                &lt;wl-button type="primary" icon="iconicon-xitongshezhi" circle &gt;&lt;/wl-button&gt;
                &lt;wl-button type="default" icon="iconicon-yunyingzhongxin" circle &gt;&lt;/wl-button&gt;
                &lt;wl-button type="warning" icon="iconfuwu" circle &gt;&lt;/wl-button&gt;
                &lt;wl-button type="danger" icon="iconicon-caidanlan-shangpin" circle &gt;&lt;/wl-button&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>
        <div class="h-blocks">
            <div class="h-title">定制化按钮</div>
            <div class="hb-a">
                <div class="hb-a-lis">
                    <Btn type="custom1">按钮1</Btn>
                    <Btn type="custom2">按钮2</Btn>
                    <Btn type="custom3">按钮3</Btn>
                    <Btn type="custom4">按钮4</Btn>
                    <Btn type="custom5">按钮5</Btn>
                    <Btn type="custom6">按钮6</Btn>
                    <Btn type="custom7">按钮7</Btn>
                    <Btn type="custom8">按钮8</Btn>
                    <Btn type="custom9">按钮9</Btn>
                </div>
            </div>

            <div class="hb-code">
                <div class="hb-cmain">
                    <div class="hb-code-desc">定制化按钮不可与其他属性混用，只有一种样式</div>
                    <div v-highlight>
                        <pre>
                            <code>
            &lt;wl-button type="custom1" &gt;按钮1&lt;/wl-button&gt;
            &lt;wl-button type="custom2" &gt;按钮2&lt;/wl-button&gt;
            &lt;wl-button type="custom3" &gt;按钮3&lt;/wl-button&gt;
            &lt;wl-button type="custom4" &gt;按钮4&lt;/wl-button&gt;
            &lt;wl-button type="custom5" &gt;按钮5&lt;/wl-button&gt;
            &lt;wl-button type="custom6" &gt;按钮6&lt;/wl-button&gt;
            &lt;wl-button type="custom7" &gt;按钮7&lt;/wl-button&gt;
            &lt;wl-button type="custom8" &gt;按钮8&lt;/wl-button&gt;
            &lt;wl-button type="custom9" &gt;按钮9&lt;/wl-button&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>

    </div>
</template>

<script>
    import Btn from '@/components/template/Button'
    export default {
        name: 'wlbutton',
        data() {
            return {

            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{
            Btn
        }
    }
</script>
<style scoped>

</style>



