<template>
    <button class="wl-btns" :class="[type,{
        'is-disabled':disabled,
        'is-plain': plain,
        'is-round': round,
        'is-circle': circle,
        }]" :disabled='disabledBtns'
            @click="handleClick"
    >
        <i class="iconfont" :class="icon" v-show="icon != ''"></i>
        <slot></slot>
    </button>
</template>
<script>
    export default {
        name: 'btns',
        props:{
            type: {
                type: String,
                default: 'default'
            },
            disabled: {
                type:Boolean,
                default:false
            },
            plain:Boolean,
            round:Boolean,
            circle:Boolean,
            icon:{
                type:String,
                default:''
            }
        },
        computed:{
            disabledBtns(){
                let flag = false;
                if(this.disabled){
                    flag = true;
                }else if(!this.disabled || this.disabled === 'false' || this.disabled == ''){
                    flag = false;
                }
                return flag;
            },

        },
        data() {
            return {

            }
        },
        mounted(){
            //console.log(this.disabled);
            //console.log(typeof this.disabled);
        },
        methods:{
            handleClick(evt) {
                this.$emit('click', evt);
            }
        }
    }
</script>
<style scoped>

</style>


